import store from "../../store";
import { bootstrap } from "../../app";
import { Emitter } from "../../mixins";
import { deepMerge, throttle } from "../../utils";
import { isArray, isString, isObject, isFunction } from "../../utils/typeCheck";
import "./styles/index.scss";

export default {
  name: "xn-crud",
  componentName: "XnCrud",
  mixins: [Emitter],
  props: {
    // 
    name: String,
    // 
    scroll: Boolean,
    //  { selection, { next } }
    onDelete: Function,
    //  { params, { next, done, render } }
    onRefresh: Function,
  },
  provide() {
    return {
      crud: this,
    };
  },
  data() {
    return {
      service: null,
      loading: false,
      upsertItems: [],
      selection: [],
      test: {
        refreshRd: null,
        sortLock: false,
        process: false,
      },
      permission: {
        update: true,
        page: true,
        info: true,
        list: true,
        add: true,
        delete: true,
      },
      params: {
        page: 1,
        size: 20,
      },
      // Config data
      dict: {
        api: {
          list: "list",
          add: "add",
          update: "update",
          delete: "delete",
          info: "info",
          page: "page",
          import: "import",
          export: "export",
        },
        pagination: {
          page: "page",
          size: "size",
        },
        search: {
          keyWord: "keyWord",
          query: "query",
        },
        sort: {
          order: "order",
          prop: "prop",
        },
        label: {
          add: "",
          delete: "",
          multiDelete: "",
          export: "",
          import: "",
          update: "",
          refresh: "",
          advSearch: "",
          saveButtonText: "",
          closeButtonText: "",
        },
      },
      table: {
        "context-menu": true,
      },
      fn: {
        permission: null,
      },
      size: "mini",
      style: {
        refreshBtn: {},
        addBtn: {},
        exportBtn: {},
        importBtn: {},
        multiDeleteBtn: {},
        advBtn: {},
        editBtn: {},
        deleteBtn: {},
        saveBtn: {},
        closeBtn: {},
        filterSearchBtn: {},
      },
      screenSize: ""
    };
  },

  created() {
    // 
    deepMerge(this, store.__crud);

    this.$on("table.selection-change", ({ selection }) => {
      this.selection = selection;
    });
    this.$on("upsert.init", ({ items }) => {
      this.upsertItems = items;
    });
  },

  mounted() {
    const res = bootstrap(this);

    // Loaded
    this.$emit("load", res);

    // 
    this.bindEvent(res);

    // 
    window.removeEventListener("resize", function () { });
    const throttleFun = throttle(() => {
      this.doLayout();
    });
    window.addEventListener("resize", throttleFun);
  },

  methods: {
    // 
    getPermission(key) {
      if (!key) {
        return this.permission;
      }

      switch (key) {
        case "edit":
        case "update":
          return this.permission["update"];
        default:
          return this.permission[key];
      }
    },

    // 
    getParams() {
      return this.params;
    },

    // 
    bindEvent(res) {
      for (let i in store.__crud.event) {
        let event = store.__crud.event[i];
        let mode = null;
        let callback = null;

        if (isObject(event)) {
          mode = event.mode;
          callback = event.callback;
        } else {
          mode = "on";
          callback = event;
        }

        if (!["on", "once"].includes(mode)) {
          return console.error(i, "mode must be (on / once)");
        }

        if (!isFunction(callback)) {
          return console.error(i, "callback is not a function");
        }

        store.__inst[`$${mode}`](i, (data) => {
          callback(data, res);
        });
      }
    },

    // 
    rowAdd() {
      this.broadcast("xn-upsert", "crud.add");
    },

    // 
    rowEdit(data) {
      this.broadcast("xn-upsert", "crud.edit", data);
    },

    // 
    rowAppend(data) {
      this.broadcast("xn-upsert", "crud.append", data);
    },

    // 
    rowClose() {
      this.broadcast("xn-upsert", "crud.close");
    },

    // 
    rowDelete(...selection) {
      // 
      const reqName = this.dict.api.delete;

      let params = {
        ids: selection.map((e) => e.id),
      };

      // 
      const next = (params) => {
        return new Promise((resolve, reject) => {
          this.$confirm(``, "", {
            type: "warning",
          })
            .then((res) => {
              if (res === "confirm") {
                // Validate
                if (!this.service[reqName]) {
                  return reject(`Request function '${reqName}' is not fount`);
                }

                // Send request
                this.service[reqName](params)
                  .then((res) => {
                    this.$message.success(``);
                    this.refresh();
                    resolve(res);
                  })
                  .catch((err) => {
                    reject(err);
                  });
              }
            })
            .catch(() => null);
        });
      };

      if (this.onDelete) {
        this.onDelete(selection, { next });
      } else {
        next(params);
      }
    },

    // 
    deleteMulti() {
      this.rowDelete.apply(this, this.selection || []);
    },

    // 
    openAdvSearch() {
      this.broadcast("xn-adv-search", "crud.open");
    },

    // 
    closeAdvSearch() {
      this.broadcast("xn-adv-search", "crud.close");
    },

    // 
    paramsReplace(params) {
      const { pagination, search, sort } = this.dict;
      let a = { ...params };
      let b = { ...pagination, ...search, ...sort };

      for (let i in b) {
        // eslint-disable-next-line
        if (a.hasOwnProperty(i)) {
          if (i != b[i]) {
            a[`_${b[i]}`] = a[i];

            delete a[i];
          }
        }
      }

      for (let i in a) {
        if (i[0] === "_") {
          a[i.substr(1)] = a[i];

          delete a[i];
        }
      }

      return a;
    },

    // 
    refresh(newParams = {}) {
      // 
      let params = this.paramsReplace(Object.assign(this.params, newParams));

      // Loading
      this.loading = true;

      // 
      let rd = (this.test.refreshRd = Math.random());

      // 
      const done = () => {
        this.loading = false;
      };

      // 
      const render = (list, pagination) => {
        this.broadcast("xn-table", "crud.refresh", { list });
        this.broadcast("xn-pagination", "crud.refresh", pagination);
        done();
      };

      // 
      const next = (params) => {
        return new Promise((resolve, reject) => {
          const reqName = this.dict.api.page;

          if (!this.service[reqName]) {
            done();
            return reject(`Request function '${reqName}' is not fount`);
          }

          this.service[reqName](params)
            .then((res) => {
              if (rd != this.test.refreshRd) {
                return false;
              }

              if (isString(res)) {
                return reject("Response error");
              }

              if (isArray(res)) {
                render(res);
              } else if (isObject(res)) {
                render(res.list, res.pagination);
              }

              resolve(res);
            })
            .catch((err) => {
              reject(err);
            })
            .done(() => {
              done();
              this.test.sortLock = true;
            });
        });
      };

      if (this.onRefresh) {
        return this.onRefresh(params, { next, done, render });
      } else {
        return next(params);
      }
    },

    // 
    doLayout() {
      this.broadcast("xn-table", "resize");
    },

    // 
    done() {
      this.test.process = true;
    },
  },

  render() {
    return this.scroll ? (
      <xn-scrollbar>
        <div class={"xn-crud"}>{this.$slots.default} </div>
      </xn-scrollbar>
    ) : (
      <div class={"xn-crud"}>{this.$slots.default}</div>
    );
  },
};
