import store from "../../store";
import { Emitter } from "../../mixins";

export default {
  name: "xn-upsert",
  componentName: "XnUpsert",
  mixins: [Emitter],
  inject: ["crud"],
  props: {
    // 
    value: {
      type: Object,
      default: () => ({}),
    },
    // 
    items: Array,
    // form
    props: {
      type: Object,
      default: () => ({}),
    },
    // 
    sync: Boolean,
    // 
    op: Object,
    // 
    dialog: Object,
    //  { isEdit, data, { submit, done, close } }
    onOpen: Function,
    //  { action, done }
    onClose: Function,
    //  { data, { next, done, close } }
    onInfo: Function,
    //  { isEdit, data, { next, done, close } }
    onSubmit: Function,
  },

  data() {
    return {
      isEdit: false,
      form: {},
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      handler(val) {
        this.form = val;
      },
    },
  },

  created() {
    this.$on("crud.add", this.add);
    this.$on("crud.append", this.append);
    this.$on("crud.edit", this.edit);
    this.$on("crud.close", this.close);
    this.dispatch("xn-crud", "upsert.init", { items: this.items });
  },

  methods: {
    // 
    async add() {
      this.isEdit = false;
      this.form = {};
      await this.open();
      this.$emit("open", false, {});
    },

    // 
    async append(data) {
      this.isEdit = false;

      if (data) {
        for (let i in data) {
          this.$set(this.form, i, data[i]);
        }
      }

      await this.open();
      this.$emit("open", false, this.form);
    },

    // 
    edit(data) {
      const { showLoading, hiddenLoading } = this.$refs["form"];

      // 
      this.isEdit = true;

      // 
      showLoading();

      // 
      if (!this.sync) {
        this.open();
      }

      // 
      const done = (data) => {
        this.$refs["form"].reBindForm(data);
        hiddenLoading();
      };

      // 
      const close = () => {
        hiddenLoading();
        this.close();
      };

      // 
      const next = (data) => {
        const { dict, service } = this.crud;

        // 
        const reqName = dict.api.info;

        return new Promise((resolve, reject) => {
          // 
          if (!service[reqName]) {
            reject(`Request function '${reqName}' is not fount!`);
            hiddenLoading();
            return null;
          }

          // 
          service[reqName]({
            id: data.id,
          })
            .then((res) => {
              done(res.info);
              resolve(res.info);

              // 
              if (this.sync) {
                this.open();
              }

              // 
              this.$emit("open", this.isEdit, this.form);
            })
            .catch((err) => {
              reject(err);
            })
            .done(() => {
              hiddenLoading();
            });
        });
      };

      if (this.onInfo) {
        this.onInfo(data, {
          next,
          done: (data) => {
            done(data);
            this.$emit("open", true, this.form);
          },
          close,
        });
      } else {
        next(data);
      }
    },

    // 
    open() {
      const { saveButtonText, closeButtonText } = this.crud.dict.label;

      return new Promise((resolve) => {
        this.$refs["form"].open({
          title: this.isEdit ? "" : "",
          props: this.props,
          on: {
            open: (_, { submit, done, close }) => {
              if (this.onOpen) {
                this.onOpen(this.isEdit, this.form, {
                  submit,
                  done,
                  close,
                });
              }

              resolve();
            },
            close: () => {
              if (this.onClose) {
                this.onClose(this.close);
              } else {
                this.close();
              }
            },
            submit: this.submit,
          },
          op: {
            saveButtonText,
            closeButtonText,
            ...this.op,
          },
          dialog: this.dialog,
          items: this.items,
          _data: {
            isEdit: this.isEdit,
          },
        });
      });
    },

    // 
    close() {
      this.$refs["form"].close();
      this.$emit("close");
    },

    /**
     * 
     * @param {object} data
     */
    submit(data, { done }) {
      const { dict, service } = this.crud;

      // 
      const next = (data) => {
        return new Promise((resolve, reject) => {
          // 
          const reqName = dict.api[this.isEdit ? "update" : "add"];

          // 
          if (!service[reqName]) {
            done();
            return reject(`Request function '${reqName}' is not fount!`);
          }

          // 
          service[reqName](data)
            .then((res) => {
              this.$message.success("");
              // 
              this.close();
              // 
              this.crud.refresh();
              // 
              resolve(res);
            })
            .catch((err) => {
              reject(err);
            })
            .done(done);
        });
      };

      // 
      if (this.onSubmit) {
        this.onSubmit(this.isEdit, data, {
          done,
          next,
          close: () => {
            this.close("submit");
          },
          $refs: store.__inst.$refs,
        });
      } else {
        next(data);
      }
    },
  },

  render() {
    return (
      <div class="xn-upsert">
        <xn-form
          ref="form"
          v-model={this.form}
          bind-component-name="XnUpsert"
          {...{
            scopedSlots: {
              ...this.$scopedSlots,
            },
          }}
        ></xn-form>
      </div>
    );
  },
};
